import { useMemo } from "react"
import { AxiosError } from "axios"
import qs from "qs"
import {
  CqsSavedQuery,
  CqsSavedStatus,
  CqsUserDataQueriesGetResponse,
  CqsUserDataQueriesPostRequest,
  CqsUserQuery,
  SharedDataPost,
} from "@rp/common/api-types"
import { mapToISOTimestamp } from "@rp/common/utils"
import { axiosClient } from "@rp/react/axios-client"
import { SnackbarEnqueueAction } from "@rp/react/common/types"
import {
  QueryClient,
  keepPreviousData,
  useMutation,
  useQueries,
  useQuery,
  useQueryClient,
} from "@tanstack/react-query"

const USER_DATA_ROOT = "/user-data/queries"

type QueryUpdate = {
  id: string
  fields: CqsUserDataQueriesPostRequest
}

type PatchContext = {
  id: string
  previous: CqsSavedQuery | undefined
  previousSavedQueries: CqsUserDataQueriesGetResponse | undefined
}

type SharedSearchUpdate = { id: string; sharedBody: SharedDataPost }

type Ownership = "user" | "company"

const getQueryKey = (savedStatus?: CqsSavedStatus, ownership?: Ownership) => {
  const queryKey = ["queries"]

  if (ownership) {
    queryKey.push(ownership)
  }

  if (savedStatus) {
    queryKey.push(savedStatus)
  }

  return queryKey
}

const getSavedQueryKey = (queryId?: string) => ["query", queryId]

// -------------------
// Network Requests
// -------------------
const getAll = async (params: {
  save_status?: CqsSavedStatus
  owned?: boolean
  shared?: "company"
}) => {
  const queryString = qs.stringify(params, { addQueryPrefix: true, skipNulls: true })
  const { data } = await axiosClient.get<CqsUserDataQueriesGetResponse>(
    `${USER_DATA_ROOT}${queryString}`,
  )
  return data
}

const getSavedQueryById = async (queryId: string) => {
  const { data } = await axiosClient.get<CqsSavedQuery>(`${USER_DATA_ROOT}/${queryId}`)
  return data
}

const create = async (query: CqsUserDataQueriesPostRequest) => {
  const { data } = await axiosClient.post<CqsSavedQuery>(USER_DATA_ROOT, query)
  return data
}

const patchSavedQuery = async ({ id, fields }: QueryUpdate) => {
  const { data } = await axiosClient.patch<CqsSavedQuery>(`${USER_DATA_ROOT}/${id}`, fields)
  return data
}

const putLastExecuted = async (id: string) => {
  return axiosClient.put<CqsSavedQuery>(`${USER_DATA_ROOT}/${id}/executed`)
}

const destroy = async (queryId: string) => {
  await axiosClient.delete(`${USER_DATA_ROOT}/${queryId}`)
}

const pin = async (queryId: string) => {
  return await axiosClient.put(`${USER_DATA_ROOT}/${queryId}/pin`)
}

const unpin = async (queryId: string) => {
  await axiosClient.delete(`${USER_DATA_ROOT}/${queryId}/pin`)
}

const reorder = async (queryIds: Array<string>) => {
  await axiosClient.post(`${USER_DATA_ROOT}/pinned`, queryIds)
}
const shareSearch = async ({ id, sharedBody }: SharedSearchUpdate) => {
  const { data } = await axiosClient.post<QueryUpdate>(`${USER_DATA_ROOT}/${id}/share`, sharedBody)
  //TODO The BE should respond with the new data, passing the id for the moment
  return { data, id }
}

// -------------------
// Hooks
// -------------------

// Get all queries
const useUserQueries = (save_status?: CqsSavedStatus, ownership?: Ownership, enabled = true) => {
  return useQuery({
    queryKey: getQueryKey(save_status, ownership),
    queryFn: () => {
      if (!ownership) {
        return getAll({ save_status })
      }

      return ownership === "user"
        ? getAll({ save_status, owned: true })
        : getAll({ save_status, shared: "company" })
    },
    staleTime: Infinity,
    gcTime: Infinity,
    retry: false,
    placeholderData: keepPreviousData,
    enabled: enabled,
    select: response => ({
      ...response,
      results: response.results.map(mapToISOTimestamp),
    }),
  })
}

// Get a query
const useSavedQuery = (queryId?: string) => {
  return useQuery({
    queryKey: getSavedQueryKey(queryId),
    queryFn: () => getSavedQueryById(queryId ?? ""),
    staleTime: Infinity,
    gcTime: Infinity,
    retry: false,
    enabled: Boolean(queryId),
    select: response => mapToISOTimestamp(response),
    // initialData: dummySavedQuery,
  })
}

const useMultipleSavedQueries = (ids: Array<string>) => {
  return useQueries({
    queries: ids.map(id => ({
      queryKey: getSavedQueryKey(id),
      queryFn: () => getSavedQueryById(id),
      staleTime: Infinity,
      select: (response: CqsSavedQuery) => mapToISOTimestamp(response),
    })),
  })
}

// Create a new Saved Query
const useCreateSavedQuery = () => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: create,
    onSuccess: query => {
      queryClient.setQueryData(getSavedQueryKey(query.id), query)
      queryClient.refetchQueries({ queryKey: getQueryKey() })
    },
  })
}

// Handle the PUT and PATCH functions
const useUpdateSavedQueryField = ({ enqueueError }: { enqueueError: SnackbarEnqueueAction }) => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: patchSavedQuery,
    onMutate: async (queryUpdate: QueryUpdate): Promise<PatchContext> => {
      await queryClient.cancelQueries({
        queryKey: getSavedQueryKey(queryUpdate.id),
      })

      // Update the query data
      const previous = queryClient.getQueryData<CqsSavedQuery>(getSavedQueryKey(queryUpdate.id))

      queryClient.setQueryData(getSavedQueryKey(queryUpdate.id), {
        ...previous,
        ...queryUpdate.fields,
      })

      // Update the saved queries list
      const previousSavedQueries = queryClient.getQueryData<CqsUserDataQueriesGetResponse>(
        getQueryKey("saved"),
      )

      if (previousSavedQueries) {
        const queryBeingUpdated = previousSavedQueries.results.find(
          query => query.id === queryUpdate.id,
        )

        queryClient.setQueryData(getQueryKey("saved"), {
          results: [
            ...previousSavedQueries.results.filter(query => query.id !== queryUpdate.id),
            {
              ...queryBeingUpdated,
              ...queryUpdate.fields,
            },
          ],
        })
      }

      return { id: queryUpdate.id, previous, previousSavedQueries }
    },
    onSuccess: result => {
      queryClient.refetchQueries({ queryKey: getQueryKey() })
      queryClient.refetchQueries({
        queryKey: ["stories for query:", result.id],
      })
      queryClient.setQueryData(getSavedQueryKey(result.id), result)
    },
    onError: (error, _, context) => {
      if (error instanceof AxiosError && error.response?.status === 403) {
        enqueueError(error?.response?.data.message)
      } else {
        enqueueError("There was an error updating your search")
      }

      if (context) {
        queryClient.setQueryData(getSavedQueryKey(context.id), context.previous)
        queryClient.setQueryData(getQueryKey("saved"), context.previousSavedQueries)
      }
    },
    retry: (count, error) => {
      if (error instanceof AxiosError && error.response?.status === 409 && count < 3) {
        return true
      }
      return false
    },
    retryDelay: 1000,
  })
}

// Update the last executed time for a query
const useUpdateLastExecuted = () => {
  return useMutation({
    mutationFn: putLastExecuted,
    retry: (count, error: AxiosError) => {
      if (error.response?.status === 409 && count < 3) {
        return true
      }
      return false
    },
    retryDelay: 1000,
  })
}

// Delete a query
const useDeleteSavedQuery = () => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: destroy,
    onMutate: queryId => {
      const previous = queryClient.getQueriesData({ queryKey: getQueryKey() })

      queryClient.setQueriesData<CqsUserDataQueriesGetResponse>(
        { queryKey: getQueryKey() },
        old => {
          if (!old) {
            return old
          }
          return { results: old?.results.filter(item => item.id !== queryId) }
        },
      )

      return previous
    },
    onError: (_, __, context) => {
      if (context) {
        for (const [queryKey, previous] of context) {
          queryClient.setQueryData(queryKey, previous)
        }
      }
    },
    onSuccess: () => queryClient.refetchQueries({ queryKey: getQueryKey() }),
  })
}

const usePinQuery = ({ enqueueError }: { enqueueError: SnackbarEnqueueAction }) => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: pin,
    onMutate: (queryId: string) => {
      const previous = queryClient.getQueriesData<CqsSavedQuery>({ queryKey: getQueryKey() })

      queryClient.setQueriesData<CqsUserDataQueriesGetResponse>(
        { queryKey: getQueryKey() },
        prev => {
          if (!prev) {
            return prev
          }

          let lastItem = 0
          let pinnedQuery = undefined
          let pinnedQueryIndex = 0
          for (const entry of Object.entries(prev.results)) {
            const [index, query] = entry

            if (query.pinIndex !== null && query.pinIndex > lastItem) {
              lastItem = query.pinIndex
            }

            if (query.id === queryId) {
              pinnedQuery = { ...query }
              pinnedQueryIndex = Number(index)
            }
          }

          if (!pinnedQuery) {
            return prev
          }

          pinnedQuery.pinned = true
          pinnedQuery.pinIndex = lastItem + 1

          return {
            results: [
              ...prev.results.slice(0, pinnedQueryIndex),
              pinnedQuery,
              ...prev.results.slice(pinnedQueryIndex + 1),
            ],
          }
        },
      )

      return previous
    },
    onSuccess: () => {
      queryClient.refetchQueries({ queryKey: getQueryKey() })
    },
    onError: (error: AxiosError<{ message: string; status: number }>, __, context) => {
      enqueueError(
        error.response?.data?.message ?? "There was an error updating your pinned searches",
      )
      if (context) {
        for (const [queryKey, previous] of context) {
          queryClient.setQueryData(queryKey, previous)
        }
      }
    },
  })
}

const useUnpinQuery = ({ enqueueError }: { enqueueError: SnackbarEnqueueAction }) => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: unpin,
    onMutate: (queryId: string) => {
      const previous = queryClient.getQueriesData<CqsSavedQuery>({ queryKey: getQueryKey() })

      queryClient.setQueriesData<CqsUserDataQueriesGetResponse>(
        { queryKey: getQueryKey() },
        prev => {
          if (!prev) {
            return prev
          }

          const pinnedQueryIndex = prev.results.findIndex(result => result.id === queryId)
          const pinnedQuery = {
            ...prev.results[pinnedQueryIndex],
            pinned: null,
            pinnedQueryIndex: null,
          }

          if (!pinnedQuery) {
            return prev
          }

          return {
            results: [
              ...prev.results.slice(0, pinnedQueryIndex),
              pinnedQuery,
              ...prev.results.slice(pinnedQueryIndex + 1),
            ],
          }
        },
      )

      return previous
    },
    onSuccess: () => {
      queryClient.refetchQueries({ queryKey: getQueryKey() })
    },
    onError: (_, __, context) => {
      enqueueError("There was an error updating your pinned searches")
      if (context) {
        for (const [queryKey, previous] of context) {
          queryClient.setQueryData(queryKey, previous)
        }
      }
    },
  })
}

const useReorderPinQueries = ({
  successMessage,
  enqueueSuccess,
  enqueueError,
}: {
  successMessage?: string
  enqueueSuccess: SnackbarEnqueueAction
  enqueueError: SnackbarEnqueueAction
}) => {
  const queryClient = useQueryClient()
  return useMutation({
    mutationFn: reorder,
    onMutate: queryIds => {
      const previous = queryClient.getQueryData<CqsSavedQuery>(getQueryKey("saved"))
      queryClient.setQueryData<CqsUserDataQueriesGetResponse>(getQueryKey("saved"), prev => {
        if (!prev) {
          return prev
        }

        const idIndexes = queryIds.reduce<Record<string, number>>((acc, id, index) => {
          // eslint-disable-next-line no-param-reassign
          acc[id] ??= index
          return acc
        }, {})

        return {
          results: prev.results.map(query =>
            idIndexes[query.id] !== undefined
              ? {
                  ...query,
                  pinIndex: idIndexes[query.id],
                }
              : {
                  ...query,
                  pinned: false,
                },
          ),
        }
      })

      return previous
    },
    onSuccess: () => {
      enqueueSuccess(successMessage ?? "The new order will be reflected on the Homepage")
      queryClient.refetchQueries({ queryKey: getQueryKey() })
    },
    onError: (_, __, context) => {
      enqueueError("There was an error updating your pinned searches")
      if (context) {
        queryClient.setQueryData(getQueryKey("saved"), context)
      }
    },
  })
}
const useSetPublicSearch = ({ enqueueError }: { enqueueError: SnackbarEnqueueAction }) => {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: shareSearch,
    onMutate: ({ id, sharedBody }) => {
      const prev: PatchContext | undefined = queryClient.getQueryData(getSavedQueryKey(id))

      if (prev) {
        // TODO: add company id to shared.company
        queryClient.setQueryData(getSavedQueryKey(id), { ...prev, shared: sharedBody })
      }

      return prev
    },
    onSuccess: ({ id }) => {
      queryClient.refetchQueries({
        queryKey: getSavedQueryKey(id),
      })
      queryClient.refetchQueries({ queryKey: getQueryKey() })
    },
    onError: (error, __, data) => {
      if (error instanceof AxiosError && error.response?.status === 403) {
        enqueueError(error?.response?.data.message)
      } else {
        enqueueError("There was an error sharing your search")
      }

      if (data) {
        queryClient.setQueryData(getSavedQueryKey(data.id), data)
      }
    },
  })
}

const getSavedQuery = async (queryClient: QueryClient, queryId: string) => {
  const fromCache = queryClient.getQueryData<CqsSavedQuery>(["query", queryId])
  if (fromCache) return fromCache
  return getSavedQueryById(queryId)
}

const sortByPinIndex = (first: CqsUserQuery, second: CqsUserQuery) =>
  (first.pinIndex ?? -1) - (second.pinIndex ?? -1)

const useSearchFeedQueries = () => {
  const { data: { results: saved = [] } = {}, isLoading } = useUserQueries("saved")
  const isLoadingQueries = isLoading
  const pinned = useMemo(() => saved.filter(i => i.pinned).sort(sortByPinIndex), [saved])
  return { saved, pinned, isLoading: isLoadingQueries, isInitialLoading: isLoading }
}

export {
  getSavedQuery,
  getSavedQueryById,
  useUserQueries,
  useSavedQuery,
  useCreateSavedQuery,
  useUpdateSavedQueryField,
  useDeleteSavedQuery,
  useMultipleSavedQueries,
  useUpdateLastExecuted,
  usePinQuery,
  useUnpinQuery,
  useReorderPinQueries,
  useSetPublicSearch,
  getQueryKey,
  getSavedQueryKey,
  useSearchFeedQueries,
}

export type { QueryUpdate }
